<template>
   <div>

        <form @submit.prevent="addTodo">
            <input v-model="title" type="text" name="title" placeholder="Add Todo...">
            <input type="submit" value="Submit" class="btn">
        </form>

   </div>
</template>

<script>
// import uuid from "uuid"
export default {
    name:'AddTodo',
    data(){
        return {
            title:''
        }
    },
    methods:{
        addTodo(e){
            e.preventDefault(); 
            const newTodo={
                // id:uuid.v4(),
                title:this.title,
                completed:false
            }
            // send
            this.$emit('add-todo', newTodo)
            this.title=""
        }
    }

}
</script>

<style scoped>
form{
    display: flex;
}
input[type="text"]{
    flex: 10;
    padding: 10px;
}
input[type="submit"]{
    flex: 2;
}

</style>